import Sidebar from "@components/sidebar";
import classnames from "classnames";
import { Editor } from "react-draft-wysiwyg";
import { useEffect, useState } from "react";
import { Controller, useForm } from "react-hook-form";
import { FormattedMessage, injectIntl } from "react-intl";
import "react-phone-number-input/style.css";
import { useDispatch, useSelector } from "react-redux";
import { Button, Form, FormGroup, Input, Label } from "reactstrap";
import { convertToRaw, EditorState } from "draft-js";
import draftToHtml from "draftjs-to-html";
import { yupResolver } from "@hookform/resolvers/yup";
import * as yup from "yup";
import Select from "react-select";
import { addDocument, addQuiz } from "../store/action";
import classNames from "classnames";
import { validateEditor } from "../../../../../helper/common";
import { update as updateIsQuizSection } from "../../../product/term/store/action";

const SidebarNewCourseDocument = ({
  open,
  toggleSidebar,
  intl,
  disable,
  setDisable,
  editorState,
  setEditorState,
}) => {
  const store = useSelector((state) => state.category);
  const [changeSelect, setChangeSelect] = useState();
  const [changeSelectProduct, setChangeSelectProduct] = useState();
  // const { section } = useSelector((state) => state.quiz);
  const lang = useSelector((state) => state.common.language);
  const product = useSelector((state) => state?.products?.data);
  const [section, setSection] = useState([]);
  const dispatch = useDispatch();
  const document_course = yup.object({
    productId: yup
      .number()
      .required(
        <FormattedMessage id="The course field is requiredis required" />
      ),
    curriculumSectionId: yup
      .number()
      .required(<FormattedMessage id="The term field is required" />),
    contenttext: yup
      .string()
      .required(<FormattedMessage id="Question field is required" />),
    answer: yup
      .string()
      .required(<FormattedMessage id="The answer field is required" />),
  });
  const {
    register,
    formState: { errors },
    handleSubmit,
    control,
    setError,
    setValue,
  } = useForm({
    resolver: yupResolver(document_course),
    mode: "all",
  });
  const [correct_answer, setcorrect_answer] = useState(" ");
  useEffect(() => {
    if (store?.err?.statusCode) {
      setDisable(false);
    }
  }, [store?.err]);

  const [valueContent, setValueContent] = useState(EditorState.createEmpty());
  const [valueShortContent, setValueShortContent] = useState(
    EditorState.createEmpty()
  );

  const handleOnchangeStateEditor = (data) => {
    setValueContent(data);
    const correct_answer =
      data && draftToHtml(convertToRaw(data.getCurrentContent()));
    setcorrect_answer(correct_answer);
  };
  const [isSubmit, setIsSubmit] = useState(false);
  const handleError = () => {
    setIsSubmit(true);
    if (validateEditor(correct_answer)) {
      setEditorState({
        classError: "invalid-editor is-invalid form-control",
        correct_answerError: (
          <FormattedMessage id="The options field is required" />
        ),
      });
    } else {
      setEditorState(null);
    }
  };

  const onSubmit = async (values) => {
    if (validateEditor(correct_answer)) {
      setEditorState({
        classError: "invalid-editor is-invalid form-control",
        correct_answerError: (
          <FormattedMessage id="The correct_answer is required" />
        ),
      });
      return;
    }
    const description =
      (valueContent &&
        draftToHtml(convertToRaw(valueShortContent?.getCurrentContent()))) ||
      null;

    try {
      dispatch(
        addQuiz({
          ...values,
          status: parseInt(values.status),
          publish: parseInt(values.publish),
          description,
          correct_answer,
          lang,
        })
      );

      if (parseInt(values.status) === 1 && parseInt(values.publish) === 1) {
        dispatch(
          updateIsQuizSection({
            id: values.curriculumSectionId,
            data: {
              isQuiz: 1,
              status: 1,
              lang,
              progress: 1,
            },
          })
        );
      }
    } catch (error) {
      console.log(error);
    }
  };

  useEffect(() => {
    if (isSubmit) {
      const check = validateEditor(correct_answer);

      if (check === true) {
        setEditorState({
          classError: "invalid-editor is-invalid form-control",
          correct_answerError: (
            <FormattedMessage id="The options field is required" />
          ),
        });
      } else {
        setEditorState({});
      }
    }
  }, [correct_answer, isSubmit]);

  return (
    <Sidebar
      size="lg"
      open={open}
      title={<FormattedMessage id="Add New Quiz" />}
      headerClassName="mb-1"
      contentClassName="pt-0"
      toggleSidebar={toggleSidebar}
    >
      <Form onSubmit={handleSubmit(onSubmit, handleError)}>
        <FormGroup>
          <Label for="contenttext">
            <FormattedMessage id="question" />{" "}
            <span className="text-danger">*</span>
          </Label>
          <Input
            name="contenttext"
            id="contenttext"
            placeholder=""
            innerRef={register(document_course.contenttext)}
            onBlur={() => {
              let contenttext = document.getElementById("contenttext");
              if (contenttext && contenttext.value) {
                contenttext.value = contenttext.value.trim();
              }
            }}
            className={classnames({ "is-invalid": errors["contenttext"] })}
          />
          <small className="text-danger">
            {errors?.contenttext && errors.contenttext.message}
          </small>
          {errors?.contenttext?.type == "validate" && (
            <small className="text-danger">
              <FormattedMessage id="Invalid contenttext" />
            </small>
          )}
        </FormGroup>

        <FormGroup>
          <Label for="answer">
            <FormattedMessage id="Right answer" />{" "}
            <span className="text-danger">*</span>
          </Label>
          <Input
            name="answer"
            id="answer"
            placeholder=""
            innerRef={register(document_course.answer)}
            onBlur={() => {
              let answer = document.getElementById("answer");
              if (answer && answer.value) {
                answer.value = answer.value.trim();
              }
            }}
            className={classnames({ "is-invalid": errors["answer"] })}
          />
          <small className="text-danger">
            {errors?.answer && errors.answer.message}
          </small>
          {errors?.answer?.type == "validate" && (
            <small className="text-danger">
              <FormattedMessage id="Invalid answer" />
            </small>
          )}
        </FormGroup>

        <FormGroup>
          <Label for="correct_answer">
            <FormattedMessage id="Options" />{" "}
            <span className="text-danger">*</span>
          </Label>

          <Editor
            stripPastedStyles={true}
            toolbar={{
              options: ["inline", "textAlign"],
              inline: {
                inDropdown: false,
                options: ["bold", "italic", "underline"],
              },
            }}
            editorState={valueContent}
            onEditorStateChange={(data) => handleOnchangeStateEditor(data)}
            toolbarClassName="toolbarClassName"
            wrapperClassName={"wrapperClassName " + editorState?.classError}
            editorClassName="editorClassName "
            name="correct_answer"
            className={classNames({
              "is-invalid": errors["correct_answer"],
            })}
          />
          {editorState && (
            <small className="text-danger">
              {editorState.correct_answerError}
            </small>
          )}
        </FormGroup>
        <FormGroup>
          <Label for="description">
            <FormattedMessage id="description" />{" "}
            <span className="text-danger"></span>
          </Label>

          <Editor
            stripPastedStyles={true}
            toolbar={{
              options: ["inline", "textAlign"],
              inline: {
                inDropdown: false,
                options: ["bold", "italic", "underline"],
              },
            }}
            editorState={valueShortContent}
            onEditorStateChange={(data) => setValueShortContent(data)}
            name="description"
            className={classNames({
              "is-invalid": errors["description"],
            })}
          />
        </FormGroup>

        <FormGroup>
          <Label>
            <FormattedMessage id="Course" />
            <span className="text-danger"> * </span>
          </Label>
          <Controller
            control={control}
            name="productId"
            render={({ field }) => {
              return (
                <Select
                  id="productId"
                  innerRef={register}
                  name="productId"
                  placeholder={<FormattedMessage id="Select Course" />}
                  className={classnames(
                    "react-select",
                    !changeSelectProduct
                      ? {
                          "is-invalid": errors["productId"],
                        }
                      : ""
                  )}
                  options={product?.map((item, index) => {
                    return {
                      value: item?.id,
                      label: item?.product_names[0]?.name || "",
                      section: item?.sections || [],
                    };
                  })}
                  classNamePrefix="select"
                  {...field}
                  onChange={(e) => {
                    setError("productId", "");
                    setValue("productId", e?.value);
                    setChangeSelectProduct(e?.value);
                    setSection(e?.section);
                  }}
                />
              );
            }}
          ></Controller>
          <small className="text-danger">
            {errors?.productId && errors.productId.message}
          </small>
        </FormGroup>

        <FormGroup>
          <Label>
            <FormattedMessage id="term" />
            <span className="text-danger"> * </span>
          </Label>
          <Controller
            control={control}
            name="curriculumSectionId"
            render={({ field }) => {
              return (
                <Select
                  id="curriculumSectionId"
                  innerRef={register}
                  name="curriculumSectionId"
                  placeholder={<FormattedMessage id="Select term" />}
                  className={classnames(
                    "react-select",
                    !changeSelect
                      ? {
                          "is-invalid": errors["curriculumSectionId"],
                        }
                      : ""
                  )}
                  options={section?.map((item, index) => {
                    return {
                      value: item?.id,
                      label: item?.title,
                      number: index + 1,
                    };
                  })}
                  classNamePrefix="select"
                  {...field}
                  onChange={(e) => {
                    setError("curriculumSectionId", "");
                    setValue("curriculumSectionId", e?.value);
                    setChangeSelect(e?.value);
                  }}
                />
              );
            }}
          ></Controller>
          <small className="text-danger">
            {errors?.curriculumSectionId && errors.curriculumSectionId.message}
          </small>
        </FormGroup>

        <FormGroup>
          <Label for="publish">
            <FormattedMessage id="publish" />{" "}
          </Label>

          <Input
            type="select"
            name="publish"
            id="publish"
            innerRef={register({ required: true })}
          >
            <option value="1">{intl.formatMessage({ id: "Allow" })}</option>
            <option value="0">
              {intl.formatMessage({ id: "Do not allow" })}
            </option>
          </Input>
        </FormGroup>

        <FormGroup>
          <Label for="status">
            <FormattedMessage id="status" />{" "}
          </Label>

          <Input
            type="select"
            name="status"
            id="status"
            innerRef={register({ required: true })}
          >
            <option value="1">{intl.formatMessage({ id: "Active" })}</option>
            <option value="0">{intl.formatMessage({ id: "Blocked" })}</option>
          </Input>
        </FormGroup>

        <div style={{ textAlign: "end" }}>
          <Button
            type="submit"
            className="mr-1"
            color="primary"
            disabled={disable}
          >
            <FormattedMessage id="Save" />
          </Button>
          <Button
            type="reset"
            color="secondary"
            outline
            onClick={toggleSidebar}
          >
            <FormattedMessage id="Cancel" />
          </Button>
        </div>
      </Form>
    </Sidebar>
  );
};

export default injectIntl(SidebarNewCourseDocument);
