import ExtensionsHeader from "@components/extensions-header";
import { useEffect, useState } from "react";
import { FormattedMessage, injectIntl } from "react-intl";
import Sidebar from "./Sidebar";
import { columns } from "./columns";
import { useDispatch, useSelector } from "react-redux";
import { getData, getSection, getDataExport } from "../store/action";
import { getData as getDataProduct } from "../../../product/store/action";
import Select from "react-select";
import DataTable from "react-data-table-component";
import { ChevronDown } from "react-feather";
import ReactPaginate from "react-paginate";
import {
  Button,
  Card,
  CardBody,
  Col,
  Input,
  Row,
} from "reactstrap";

import "@styles/react/libs/react-select/_react-select.scss";
import "@styles/react/libs/tables/react-dataTable-component.scss";
import { toast } from "react-toastify";
import XLSX from "xlsx";

const CustomHeader = ({ toggleSidebar, exportToExcel }) => {
  return (
    <div className="invoice-list-table-header w-100 mr-1 ml-50 mt-1 mb-75">
      <Row className="justify-content-end">
        <Col
          xl="6"
          className="d-flex align-items-sm-center justify-content-lg-end justify-content-start flex-lg-nowrap flex-wrap flex-sm-row flex-column gap-3 pr-lg-1 p-0 mt-lg-0 mt-1"
        >
          <Button.Ripple color="primary" onClick={exportToExcel}>
            Export Excel
          </Button.Ripple>
          <Button.Ripple color="primary" onClick={toggleSidebar}>
            <FormattedMessage id="Add" />
          </Button.Ripple>
        </Col>
      </Row>
    </div>
  );
};

const CourseDocumentList = ({ intl }) => {
  const dispatch = useDispatch();
  const store = useSelector((state) => state.quiz);
  const [searchTerm, setSearchTerm] = useState("");
  const [currentPage, setCurrentPage] = useState(1);
  const [rowsPerPage, setRowsPerPage] = useState(20);
  const [sidebarOpen, setSidebarOpen] = useState(false);
  const [disable, setDisable] = useState(false);
  const lang = useSelector((state) => state.common.language);
  const product = useSelector((state) => state?.products?.data);

  const [editorState, setEditorState] = useState(null);
  const [filterStatus, setFilterStatus] = useState({
    value: "",
    label: <FormattedMessage id="status" />,
  });

  const [filterPublish, setFilterPublish] = useState({
    value: "",
    label: <FormattedMessage id="publish" />,
    // number: 0,
  });
  const [searchName, setSearchName] = useState(null);
  const optionProduct = {
    value: "",
    label: <FormattedMessage id="Select Course" />,
    section: [],
  };
  const [filterProduct, setFilterProduct] = useState(optionProduct);
  const optionSection = {
    value: "",
    label: <FormattedMessage id="Select term" />,
  };
  const [section, setSection] = useState([]);
  const [filterSection, setFilterSection] = useState(optionSection);
  const [loadData, setLoadData] = useState(false);
  const toggleSidebar = () => {
    setSidebarOpen(!sidebarOpen);
    setDisable(false);
    setEditorState(null);
  };

  useEffect(() => {
    dispatch(
      getData({
        filter: {
          status: filterStatus.value || undefined,
          contenttext: searchName || undefined,
          publish: filterPublish.value || undefined,
          curriculumSectionId: filterSection.value || undefined,
          lang: lang,
        },
        skip: (currentPage - 1) * rowsPerPage,
        limit: rowsPerPage,
        order: [
          {
            key: "status",
            value: "desc",
          },
        ],
      })
    );
  }, [loadData, currentPage, rowsPerPage, lang]);

  useEffect(() => {
    dispatch(
      getSection({
        filter: {},
        skip: (currentPage - 1) * rowsPerPage,
        limit: rowsPerPage,
        order: [],
      })
    );
  }, [loadData, currentPage, rowsPerPage, lang]);

  useEffect(() => {
    dispatch(
      getDataProduct({
        filter: {
          status: 1,
          lang: lang,
          product_type: 0,
        },
        skip: (currentPage - 1) * rowsPerPage,
        limit: rowsPerPage,
        order: [
          {
            key: "id",
            value: "desc",
          },
        ],
      })
    );
    dispatch(
      getDataExport({
        filter: {
          lang: lang,
        },
        order: [
          {
            key: "status",
            value: "desc",
          },
        ],
      })
    );
  }, [lang]);

  useEffect(() => {
    if (filterProduct.value) {
      if (filterProduct?.section?.length > 0) {
        setSection(filterProduct?.section);
      }
    } else {
      setFilterSection(optionSection);
      setSection([]);
    }
  }, [filterProduct.value]);

  useEffect(() => {
    switch (store?.type) {
      case "ADD_QUIZ":
        if (store?.status == 200) {
          toast.success(<FormattedMessage id={"successfully added new!"} />);
          setLoadData(!loadData);
          toggleSidebar();
        } else if (store?.status == 400) {
          toast.warn(<FormattedMessage id={"Add new failure!"} />);
        }
        break;
      case "UPDATE_QUIZ":
        if (store?.status == 200) {
          toast.success(<FormattedMessage id={"Update successful!"} />);
          setLoadData(!loadData);
        } else if (store?.status == 400) {
          toast.warn(<FormattedMessage id={"Update failed!"} />);
        }
        break;
      default:
        break;
    }
  }, [store.status]);

  const statusOptions = [
    { value: "", label: <FormattedMessage id="Select status" /> },
    { value: "0", label: <FormattedMessage id="Blocked" /> },
    { value: "1", label: <FormattedMessage id="Active" /> },
  ];

  const publishOptions = [
    { value: "", label: <FormattedMessage id="Select Publish" /> },
    { value: "0", label: <FormattedMessage id="Do not allow" /> },
    { value: "1", label: <FormattedMessage id="Allow" /> },
  ];

  const handlePerPage = (e) => {
    const value = parseInt(e.currentTarget.value);
    setRowsPerPage(value);
  };
  const count = Number(Math.ceil(store.total / rowsPerPage));
  const dataToRender = () => {
    if (store?.data?.length > 0) {
      return store?.data;
    }
  };

  const exportToExcel = () => {
    if (store?.dataExport?.length > 0) {
      const data = store?.dataExport;
      const cols = [
        { header: "ID", key: "id" },
        { header: "Kho hc", key: "product" },
        { header: "Hc phn", key: "curriculum_section" },
        { header: "Tiu ", key: "title" },
        { header: "M t", key: "description" },
        { header: "Cu hi", key: "contenttext" },
        { header: "Cu tr li ng", key: "answer" },
        { header: "Cu tr li", key: "correct_answer" },
        { header: "Trng thi", key: "status" },
        { header: "Ngn ng", key: "lang" },
        { header: "Ngy to", key: "createdAt" },
        { header: "Ngy cp nht", key: "updatedAt" },
      ];
      const mappedData = data.map((item) =>
        Object.fromEntries(
          Object.entries(item).map(([key, val]) => {
            if (key === "product" && val) {
              return [
                cols.find((col) => col.key === key)?.header || key,
                val?.product_names?.length > 0 && val?.product_names[0]?.name,
              ];
            }
            if (key === "curriculum_section" && val) {
              return [
                cols.find((col) => col.key === key)?.header || key,
                val?.title && val?.title,
              ];
            }
            return [cols.find((col) => col.key === key)?.header || key, val];
          })
        )
      );

      const worksheet = XLSX.utils.json_to_sheet(mappedData, {
        header: cols.map((col) => col.header),
      });
      const workbook = XLSX.utils.book_new();
      XLSX.utils.book_append_sheet(workbook, worksheet, "Bi kim tra");
      XLSX.writeFile(workbook, "quiz.xlsx");
    }
  };

  return (
    <div className="app-user-list">
      <ExtensionsHeader title={<FormattedMessage id="Test" />} />
      <Card>
        <CardBody>
          <Row>
            <Col md="4">
              <Select
                isClearable={false}
                className="react-select"
                classNamePrefix="select"
                options={[
                  optionProduct,
                  ...product?.map((item, index) => {
                    return {
                      value: item?.id,
                      label: item?.product_names[0]?.name || "",
                      section: item?.sections || [],
                    };
                  }),
                ]}
                value={filterProduct}
                onChange={(data) => {
                  setFilterProduct(data);
                }}
              />
            </Col>

            <Col md="4">
              <Select
                isClearable={false}
                className="react-select"
                classNamePrefix="select"
                options={[
                  optionSection,
                  ...section?.map((item, index) => {
                    return {
                      value: item?.id,
                      label: item?.title || "",
                    };
                  }),
                ]}
                value={filterSection}
                onChange={(data) => {
                  setFilterSection(data);
                }}
              />
            </Col>

            <Col md="4">
              <Input
                id="search-invoice"
                className=" w-100"
                placeholder={intl.formatMessage({ id: "question" })}
                type="text"
                value={searchName}
                onChange={(e) => {
                  setSearchName(e.target.value);
                }}
              />
            </Col>
            <Col md="4">
              <Select
                isClearable={false}
                className="react-select"
                classNamePrefix="select"
                options={publishOptions}
                value={filterPublish}
                onChange={(data) => {
                  setFilterPublish(data);
                }}
              />
            </Col>
            <Col md="4">
              <Select
                isClearable={false}
                className="react-select"
                classNamePrefix="select"
                options={statusOptions}
                value={filterStatus}
                onChange={(data) => {
                  setFilterStatus(data);
                }}
              />
            </Col>
            <Col md="4">
              <Button
                color="primary"
                onClick={() => {
                  setLoadData(!loadData);
                  setCurrentPage(1);
                }}
              >
                <FormattedMessage id="Search" />
              </Button>
            </Col>
          </Row>
        </CardBody>
      </Card>
      <Card>
        <DataTable
          noHeader
          subHeader
          responsive
          highlightOnHover
          noDataComponent={
            <div className="sc-fznWqX gnahTY">
              <div className="sc-AxjAm gIMaKV rdt_Table" role="table">
                <div className="sc-fzqARJ icdHOq">
                  <div
                    style={{
                      padding: "25px ",
                      textAlign: "center",
                      color: "black",
                      background: "white",
                    }}
                  >
                    <FormattedMessage id={"There are no records to display"} />
                  </div>
                </div>
              </div>
            </div>
          }
          paginationServer
          columns={columns}
          sortIcon={<ChevronDown />}
          className="react-dataTable"
          data={dataToRender()}
          subHeaderComponent={
            <CustomHeader
              toggleSidebar={toggleSidebar}
              handlePerPage={handlePerPage}
              rowsPerPage={rowsPerPage}
              searchTerm={searchTerm}
              exportToExcel={exportToExcel}
            />
          }
        />
      </Card>
      {count > 0 && (
        <ReactPaginate
          previousLabel={""}
          nextLabel={""}
          pageCount={count || 0}
          activeClassName="active"
          forcePage={currentPage !== 0 ? currentPage - 1 : 0}
          onPageChange={(page) => setCurrentPage(page.selected + 1)}
          pageClassName={"page-item"}
          nextLinkClassName={"page-link"}
          nextClassName={"page-item next"}
          previousClassName={"page-item prev"}
          previousLinkClassName={"page-link"}
          pageLinkClassName={"page-link"}
          containerClassName={
            "pagination react-paginate justify-content-center my-2 pr-1"
          }
        />
      )}
      <Sidebar
        open={sidebarOpen}
        toggleSidebar={toggleSidebar}
        {...{
          disable,
          setDisable,
          editorState,
          setEditorState,
        }}
      />
    </div>
  );
};

export default injectIntl(CourseDocumentList);
