import { FormattedMessage } from "react-intl";
import { Link } from "react-router-dom";
import { Badge } from "reactstrap";

const statusObj = {
  1: {
    class: "light-success",
    text: <FormattedMessage id="Active" />,
  },
  0: {
    class: "light-secondary",
    text: <FormattedMessage id="Blocked" />,
  },
};

const publishObj = {
  1: {
    class: "light-success",
    text: <FormattedMessage id="Allow" />,
  },
  0: {
    class: "light-warning",
    text: <FormattedMessage id="Do not allow" />,
  },
};

export const columns = [
  {
    name: <FormattedMessage id="question" />,
    minWidth: "120px",
    selector: "contenttext",
    cell: (row) => {
      let maxLength = 50; // Gi s  di ti a ca chui l 20 k t.
      let contentString = row?.contenttext;
      if (contentString.length > maxLength) {
        contentString = contentString.slice(0, maxLength) + "...";
      }
      return contentString;
    },
  },
  {
    name: <FormattedMessage id="Description" />,
    minWidth: "120px",
    selector: "description",
    cell: (row) => {
      let maxLength = 40; // Gi s  di ti a ca chui l 20 k t.
      let contentString = row?.description;
      if (contentString.length > maxLength) {
        contentString = contentString.slice(0, maxLength) + "...";
      }
      return (
        <span
          dangerouslySetInnerHTML={{
            __html: contentString,
          }}
        ></span>
      );
    },
  },
  {
    name: <FormattedMessage id="Course" />,
    minWidth: "120px",
    selector: "course",
    cell: (row) =>
      row?.product?.product_names?.length > 0 &&
      row?.product?.product_names[0]?.name,
  },
  {
    name: <FormattedMessage id="publish" />,
    minWidth: "120px",
    selector: "publish",
    cell: (row) => (
      <Badge className="text-capitalize" color={publishObj[row.publish]?.class}>
        {publishObj[row.publish]?.text}
      </Badge>
    ),
  },
  {
    name: <FormattedMessage id="status" />,
    minWidth: "170px",
    selector: "status",
    cell: (row) => (
      <Badge className="text-capitalize" color={statusObj[row.status]?.class}>
        {statusObj[row.status]?.text}
      </Badge>
    ),
  },
  {
    name: <FormattedMessage id="action" />,
    minWidth: "100px",

    cell: (row) => {
      return (
        row?.id && (
          <div className="mh-100 d-flex align-items-center justify-content-center mw-100">
            <Badge
              className=" w-100"
              pill
              color="primary"
              style={{ padding: "10px 25px" }}
            >
              <Link
                className="text-edit hover:text-white"
                to={`/apps/course/test/edit/${row.id}`}
              >
                {" "}
                <FormattedMessage id="edit" />
              </Link>
            </Badge>
          </div>
        )
      );
    },
  },
];
